<template>
    <v-container fluid class="container">
    <div class="welcome">
        <transition name="fade">
            <div v-if="site !== '1'" class="text_align_center">
                안녕하세요!<br/>
                <!--서초구 납품 운동남12 안보이게 처리-->
                <span class="username">
                    {{username}}
                </span>
                <span>님</span>
            </div>
            <div v-else class="text_align_center">
                <span class="username">안녕하세요!</span>
            </div>
        </transition>
    </div>
    </v-container>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
    name: 'Welcome',
    data:()=>({
        username: null,
        site: process.env.VUE_APP_SITE
    }),
    computed: {
        ...mapGetters([
            'GET_APP_MEMBER_INFO',
        ])
    },
    watch:{
    },
    mounted() {
        this.CheckLogin()
    },
    methods: {
        CheckLogin(){
            if(this.GET_APP_MEMBER_INFO === null){
                this.$toastr.error  ( 
                    '로그인 오류', 
                    '로그인 멤버 정보가 없습니다.' 
                    ,{
                        position: 'toast-top-center',
                        clickClose: true
                    }
                )
                this.$router.push('/')
            }else{
                this.username = this.GET_APP_MEMBER_INFO.memName
                setTimeout( () => {
                    if(process.env.VUE_APP_MODE === '2'){
                        this.$router.push('/user/mainGX')
                    }else{
                        this.$router.push('/user/main')
                    }
                }, 1500)
            }
        }
    },
}
</script>

<style scoped lang="scss">
.welcome {
    display: flex; 
    justify-content: center; 
    align-items: center;
    align-content: center;
    font-size: 48px;
    height: 90vh;
    animation: fadein 2s;

    @keyframes fadein {
        from {
            opacity:0;
        }
        to {
            opacity:1;
        }
    }

    .username {
        font-size: 72px;
    }
}
.fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}
</style>